import React from "react";
import { Form } from "../common/forms";
import Joi from "joi-browser";
import cardServices from "../common/services/cardsServices";

class EditCardImg extends Form {
  state = {
    formData: {
      bizImage: [],
      bizImageDefault: [],
      bizImageWeek: [],
    },
    errors: {},
  };

  schema = {
    bizImage: Joi.array(),
    bizImageDefault: Joi.array(),
    bizImageWeek: Joi.array(),
  };

  doSubmit = async () => {
    const { formData } = this.state;
    console.log(formData, this.props.match.params.id);
    const res = await cardServices.updateCardImage(
      this.props.match.params.id,
      formData
    );
    
    console.log(res);
  };

  render() {
    return (
      <div className="container">
        <div className="row ">
          <form onSubmit={this.handelSubmit} className="col-12 mt-5 ">
            <div className="row justify-content-md-center">
              <div className="col-12 col-md-6 col-lg-4">
                {this.renderInputFile("bizImage", "bizImage", "file")}
              </div>
              <div className="col-12 col-md-6 col-lg-4">
                {this.renderInputFile(
                  "bizImageDefault",
                  "bizImageDefault",
                  "file"
                )}
              </div>
              <div className="col-12 col-md-6 col-lg-4">
                {this.renderInputFile("bizImageWeek", "bizImageWeek", "file")}
              </div>
              <div className="col-12 col-md-6 col-lg-4 mt-3 ">
                {this.renderButton("העלה תמונות חדשות ")}
              </div>
            </div>
          </form>
        </div>
      </div>
    );
  }
}

export default EditCardImg;
