import React from "react";
import { Link } from "react-router-dom";

const NavBar = ({ user, categories }) => {
  return (
    <>
      <nav className="navbar navbar-expand-sm navbar-dark bg-primary">
        <div className="container">
          <Link to="/" className="navbar-brand">
            אינדקס העסקים ביתר עילית
          </Link>
          <button
            className="navbar-toggler"
            type="button"
            data-toggle="collapse"
            data-target="#navbarManager"
            aria-controls="navbarManager"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon"></span>
          </button>

          <div className="collapse navbar-collapse" id="navbarManager">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item active dropdown">
                <Link
                  className="nav-link dropdown-toggle"
                  to="#"
                  id="dropdown01"
                  data-toggle="dropdown"
                  aria-haspopup="true"
                  aria-expanded="true"
                >
                  תפריט ניוט באתר
                </Link>
                <div className="dropdown-menu" aria-labelledby="dropdown01">
                  {categories?.length > 0 &&
                    categories.map((item) => {
                      return (
                        <Link
                          key={item._id}
                          className="dropdown-item"
                          to={`/category/${item.categoryName}`}
                        >
                          {item.categoryName}
                        </Link>
                      );
                    })}

                  {!user && (
                    <>
                      <Link className="dropdown-item" to="/login">
                        התחברות
                      </Link>
                      <Link className="dropdown-item" to="/sign-up-business">
                        הרשמה לבעלי עסקים
                      </Link>
                      <Link className="dropdown-item" to="/sign-up-user">
                        הרשמה ללקוחות
                      </Link>
                      <Link className="dropdown-item" to="/sign-up-manager">
                        הרשמה למנהלים
                      </Link>
                    </>
                  )}
                  {user && (
                    <>
                      <Link className="dropdown-item" to="/logout">
                        התנתקות
                      </Link>
                      <Link className="dropdown-item" to="/myFavorite">
                        המעודפים שלי
                      </Link>
                    </>
                  )}
                </div>
              </li>
            </ul>
          </div>
        </div>
      </nav>
    </>
  );
};

export default NavBar;
